// main page elements
body {
	font-size: 14px;
	// color: $dark
	font-family: 'ALS Schlange sans';
	font-style: normal;
	font-weight: normal;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1440px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	position: relative;
	max-width: 1270px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }
div {
	z-index: 1; }

.hidden {
	display: none; }

.main-title {
	color: $text;
	font-weight: bold;
	font-size: 64px;
	line-height: 110%; }
.dop-title {
	font-weight: bold;
	font-size: 48px;
	line-height: 110%;
