.text {
	color: $dark-grey;
	font-size: 18px;
	line-height: 150%; }
.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	color: #FFFFFF;
	font-weight: bold;
	font-size: 18px;
	line-height: 150%;
	background: #A755ED;
	box-shadow: inset 5px 5px 5px rgba(255, 255, 255, 0.2), inset -5px -5px 5px rgba(0, 0, 0, 0.1);
	border-radius: 16px;
	width: 180px;
	height: 56px; }
.flex {
