//start form
form {
	display: flex;
	justify-content: center;
	gap: 70px; }
.form {
	padding-top: 140px;
	padding-bottom: 140px;
	h2 {
		text-align: center;
		margin-bottom: 30px; }
	p {
		max-width: 693px;
		width: 100%;
		text-align: center;
		margin: 0 auto;
		margin-bottom: 60px; }
	.inputs-left {
		display: flex;
		flex-direction: column;
		gap: 30px; } }
.textarea_block {
	max-width: 430px;
	width: 100%;
	position: relative;
	height: 287px; }
.textarea_block textarea {
	display: block;
	padding: 0;
	width: 430px;
	height: 287px;
	border: none;
	border: 1px solid #B0BEC5;
	border-radius: 10px;
	background: none;
	font-size: 24px;
	resize: none;
	outline: none;
	margin: 0px;
	font-size: 18px;
	line-height: 150%;
	color: $text;
	padding-left: 20px;
	padding-right: 20px;
	padding-bottom: 15px;
	padding-top: 15px; }
.textarea_block label {
	display: block;
	width: 430px;
	height: 287px;
	position: absolute;
	top: 0;
	left: 20px;
	cursor: text; }
.textarea_block label span {
	display: block;
	height: 24px;
	-webkit-transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	transition: all 0.3s ease;
	font-size: 18px;
	line-height: 150%;
	color: $text;
	position: absolute;
	top: 15px;
	left: 0;
	cursor: text; }
.textarea_block textarea:focus+label span {
	font-weight: bold;
	font-size: 14px;
	line-height: 150%;
	color: $primary;
	top: 2px; }
.textarea_block+label span {
	font-weight: bold;
	font-size: 14px;
	line-height: 150%;
	color: $primary;
	top: 2px; }
.input_block {
	max-width: 430px;
	height: 57px;
	position: relative; }
.input_block input[type="text"] {
	font-size: 18px;
	line-height: 150%;
	color: $text;
	padding-left: 20px; }
.input_block input {
	display: block;
	padding: 0;
	width: 430px;
	height: 57px;
	border: none;
	border: 1px solid #B0BEC5;
	border-radius: 10px;
	background: none;
	font-size: 24px;
	color: #000;
	outline: none; }
.input_block label {
	display: block;
	width: 430px;
	height: 57px;
	position: absolute;
	top: 0;
	left: 20px;
	cursor: text; }
.input_block label span {
	display: block;
	height: 24px;
	-webkit-transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	transition: all 0.3s ease;
	font-size: 18px;
	line-height: 150%;
	color: $text;
	position: absolute;
	top: 15px;
	left: 0;
	cursor: text; }
.input_block input:focus+label span {
	font-weight: bold;
	font-size: 14px;
	line-height: 150%;
	color: $primary;
	top: 2px; }
.has_value+label span {
	font-weight: bold;
	font-size: 14px;
	line-height: 150%;
	color: $primary;
	top: 2px; }
//end form
